
import { Injectable } from '@angular/core';

import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { AuthService } from '../services/auth.service';

/**
 * Garde de rle pour protger les routes en fonction des rles utilisateur
 *
 * Ce garde vrifie si l'utilisateur connect possde au moins un des rles
 * requis pour accder  une route spcifique. Si l'utilisateur n'est pas
 * connect ou ne possde pas les rles ncessaires, il sera redirig.
 */
@Injectable({
  providedIn: 'root'
})
export class RoleGuard implements CanActivate {
  /**
   * Constructeur du garde de rle
   *
   * @param authService - Service d'authentification pour accder  l'utilisateur courant
   * @param router - Service de routage pour les redirections
   */
  constructor(
    private authService: AuthService,
    private router: Router
  ) { }

  /**
   * Mthode principale du garde qui dtermine si l'accs  la route est autoris
   *
   * @param route - Instantan de la route active contenant les donnes de route
   * @param state - tat actuel du routeur
   * @returns Observable<boolean> - Observable qui met true si l'accs est autoris, false sinon
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> {
    // Rcupration des rles requis depuis les donnes de la route
    const requiredRoles = route.data['roles'] as string[];


    // Observation de l'utilisateur courant via le service d'authentification
    return this.authService.currentUser$.pipe(
      // Prend seulement la premire mission puis se dsabonne
      take(1),
      map(user => {
        console.log('RoleGuard: User:', user, 'Required Roles:', requiredRoles); // Log de dbogage

        // Vrification si l'utilisateur est connect
        if (!user) {
          console.log('RoleGuard: No user, redirecting to /login');
          // Redirection vers la page de connexion si aucun utilisateur n'est connect
          this.router.navigate(['/login']);
          return false;
        }

        // Vrification si l'utilisateur possde au moins un des rles requis
        const hasRequiredRole = requiredRoles.some(role =>
          user.roles.includes(role)
        );

        // Redirection vers la page 403 (Accs interdit) si l'utilisateur n'a pas les rles ncessaires
        if (!hasRequiredRole) {
          console.log('RoleGuard: User lacks required role, redirecting to /403');
          this.router.navigate(['/403']);
          return false;
        }

        // Accs autoris si l'utilisateur a les rles requis
        console.log('RoleGuard: Access granted');
        return true;
      })
    );
  }
}